import React from "react";
import classes from "./Post.module.css";

const Post = ({ message, likeCounter }) => {
  let { item, avatar } = classes;
  return (
    <div>
      <div className={item}>
        <img
          className={avatar}
          src="data:image/png;base64,"
          alt=""
        />
        {message} {likeCounter}
      </div>
    </div>
  );
};

export default Post;
